.loading_spinner-wrap {
  width: 100%;
  padding: 50px 0;
}

.loading_spinner {
  display: block;
  margin: 0 auto;
  fill: #000;
}

.loading_spinner circle {
    -webkit-animation-name: upAndDown;
            animation-name: upAndDown;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    -webkit-animation-timing-function: cubic-bezier(0.05, 0.2, 0.35, 1);
            animation-timing-function: cubic-bezier(0.05, 0.2, 0.35, 1);
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}

.loading_spinner circle:nth-child(2) {
  -webkit-animation-delay: 0.18s;
          animation-delay: 0.18s;
}

.loading_spinner circle:nth-child(3) {
  -webkit-animation-delay: 0.36s;
          animation-delay: 0.36s;
}

@-webkit-keyframes upAndDown {
  0% {
    opacity: 0;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  25% {
    opacity: 1;
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
  75% {
    opacity: 1;
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
  100% {
    opacity: 0;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes upAndDown {
  0% {
    opacity: 0;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  25% {
    opacity: 1;
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
  75% {
    opacity: 1;
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
  100% {
    opacity: 0;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

.container {
  position: relative;
  min-height: 65vh;
  /* height: 45vh; */
  max-width: 800px;
  margin-bottom: 5vh;
  border-radius: 3px;
}

.choice-list {
  /* margin: 0; */
  padding: 0;
  list-style: none;
}

.question {
  margin: 0;
  padding: 0.5rem 2.5rem 1.5rem 2.5rem;
}

/* .choice-item {
  border-top: 1px solid #eee;
} */

.choice-item:hover {
  background-color: #eefbfe;
}

.radioCustomButton, .radioCustomButtonSuccess {
  position: absolute;
  width: auto;
  opacity: 0;
}

.radioCustomButton,
.radioCustomButtonSuccess,
.radioCustomLabel {
  display: inline-block;
  vertical-align: middle;
  cursor: pointer;
}

.radioCustomLabel {
  position: relative;
  width: -webkit-fill-available;
  width: -moz-available;
  /* width: 100%; */
  margin: 0;
  padding: 1.5rem 0 1.5rem 0;
  line-height: 1.5;
  /* text-align: justify;
  padding-left: 20%; */
}

.radioCustomButton ~ .radioCustomLabel:before, .radioCustomButtonSuccess ~ .radioCustomLabel:before {
  position: absolute;
  /* top: 2.6vh; */
  /* top: 21px; */
  /* left: 0; */
  left: 38px;
  width: 28px;
  height: 28px;
  content: '';
  display: inline-block;
  vertical-align: middle;
  background: #fff;
  border: 1px solid #bbb;
  border-radius: 50%;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

.radioCustomButton:checked ~ .radioCustomLabel:before {
  content: '';
  background: #776859 url(/static/media/multiply.0e00f3f4.svg) no-repeat;
  background-size: 27px;
  border-color: #776859;
}

.radioCustomButtonSuccess:checked ~ .radioCustomLabel:before {
  content: '';
  background: #77b723 url(/static/media/icon-check.4cffa739.svg) no-repeat;
  background-size: 27px;
  border-color: #6aa71a;
}

/* Animation */
.fade-enter {
  opacity: 0;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  -webkit-transition: opacity 0.5s ease-in-out 0.3s;
  transition: opacity 0.5s ease-in-out 0.3s;
}

.fade-leave {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  opacity: 1;
}

.fade-leave.fade-leave-active {
  opacity: 0;
  -webkit-transition: opacity 0.5s ease-in-out;
  transition: opacity 0.5s ease-in-out;
}

.fade-appear {
  opacity: 0;
}

.fade-appear.fade-appear-active {
  opacity: 1;
  -webkit-transition: opacity 0.5s ease-in-out;
  transition: opacity 0.5s ease-in-out;
}

a:hover {
  text-decoration: underline;
}

ul {
  padding: 0;
}

li {
  padding-bottom: 2vh;
  list-style: none;
}

@media (min-width: 769px) {
  .container {
    margin: 2.5rem auto;
  }
}

@media (max-width: 530px) {
	.radioCustomButton ~ .radioCustomLabel:before, .radioCustomButtonSuccess ~ .radioCustomLabel:before {
		position: absolute;
		top: 3vh;
		left: 0;
	}

	.radioCustomLabel {
		text-align: justify;
		padding-left: 20%;
	}
}

